* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

input,
button,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  appearance: none;
  color: currentColor;
}

body {
  font-family: "Nunito", sans-serif;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 1.3;
  background-color: rgb(5, 129, 123);
  font-family: "Patua One", cursive;
  font-family: "Roboto Slab", serif;
}

.container {
  width: 80%;
  margin: 0 auto;
}

.header {
  position: relative;
}

.title-one {
  color: rgb(65, 17, 238);
  text-align: center;
  background-color: rgb(246, 250, 37);
  padding: 16px 0;
  border-radius: 10px;
  letter-spacing: 0.4em;
  font-size: 40px;
}

.questions__wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(5, 129, 123);
  color: rgb(17, 104, 10);
  border-radius: 10px;
}

.question {
  position: relative;
  width: 80%;
  height: 60px;
  font-size: 40px;
  border-radius: 10px;
  border-radius: 10px;
  text-align: center;
  transition: 1s;
  transform-style: preserve-3d;
  cursor: pointer;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.5);
}

.fase,
.description {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transition: 1s;
  backface-visibility: hidden;
  border-radius: 10px;
}

.fase {
  background-color: rgb(198, 14, 235);
  padding: 20px;
}

.description {
  background-color: rgb(146, 3, 127);
  transform: rotateX(180deg);
  color: #fff;
  text-align: center;
}

.error-btn {
  position: absolute;
  width: 40px;
  height: 40px;
  background-color: rgb(248, 24, 8);
  border: 1px solid rgb(10, 10, 10);
  border-radius: 6px;
  opacity: 0.1;
}
.error-btn::before, .error-btn::after {
  content: "";
  position: absolute;
  width: 80px;
  height: 6px;
  background-color: rgb(0, 0, 0);
}
.error-btn::before {
  transform: rotate(45deg);
  top: 16px;
  left: -18px;
}
.error-btn::after {
  transform: rotate(-45deg);
  top: 16px;
  left: -18px;
}
.error-btn--one {
  top: 140px;
  left: 0;
}
.error-btn--two {
  top: 242px;
  left: 0;
}
.error-btn--three {
  top: 354px;
  left: 0;
}
.error-btn--four {
  top: 140px;
  right: 0;
}
.error-btn--five {
  top: 242px;
  right: 0;
}
.error-btn--six {
  top: 354px;
  right: 0;
}

.question-btn {
  display: block;
  position: absolute;
  top: 4.4%;
  right: 10.4%;
  border: 1px solid rgba(255, 255, 255, 0.432);
  background-color: rgb(246, 250, 37);
  width: 60px;
  height: 60px;
  border-radius: 6px;
}

.arr {
  position: absolute;
  width: 20px;
  height: 2px;
  border-color: #000;
  top: -10%;
  left: 50%;
  transform: translateX(-50%);
}
.arr::after {
  position: absolute;
  content: "";
  width: 20px;
  height: 2px;
  border-color: #000;
  transform: rotate(45deg);
}

.balls__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
}

.ball {
  width: 100px;
  height: 60px;
  border: 3px solid #ccc;
  border-radius: 10px;
  font-size: 38px;
  color: #fff;
  text-align: center;
  font-weight: 600;
  background-color: rgb(5, 129, 123);
}

.answers__wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.number {
  color: #fff;
  margin-right: 20px;
}

.card {
  position: relative;
  width: 80%;
  height: 80px;
  font-size: 40px;
  margin-bottom: 28px;
  border-radius: 10px;
  border-radius: 10px;
  transition: 1s;
  transform-style: preserve-3d;
  cursor: pointer;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.5);
}

.front,
.back {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transition: 1s;
  backface-visibility: hidden;
  border-radius: 10px;
}

.front {
  background-color: rgb(67, 70, 243);
  text-align: center;
  font-weight: 800;
  padding: 20px;
}

.back {
  background-color: rgb(5, 5, 5);
  transform: rotateX(180deg);
  padding-left: 20px;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.visual {
  opacity: 1;
}

.is-flipped {
  transform: rotateX(180deg);
}